/* App.css */
body {
  margin: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to right, #2196f3, #4caf50);
}

.calculator {
  max-width: 400px;
  width: 80%;
  background-color: #000;
  border-radius: 10px;
  overflow: hidden;
}

.display {
  background-color: #2196f3;
  color: #fff;
  text-align: right;
  padding: 20px;
  font-size: 1.8em;
  overflow: hidden;
  white-space: nowrap;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

button {
  background-color: #333;
  color: #fff;
  border: none;
  font-size: 1.5em;
  padding: 20px;
  cursor: pointer;
  /* Add this line to make text visible */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.clear {
  grid-column: span 2;
}
